* {
    box-sizing: border-box;
}

:root {
    --clr-primary-red-hsl: 0, 100%, 67%;
    --clr-primary-orange-hsl: 39, 100%, 56%;
    --clr-primary-teal-hsl: 166, 100%, 37%;
    --clr-primary-blue-hsl: 234, 85%, 45%;

    --bg-gradient: linear-gradient(hsl(252, 100%, 67%), hsl(241, 81%, 54%));
    --circle-gradient: linear-gradient(hsla(256, 72%, 46%, 1), hsla(241, 72%, 46%, 0));

    --clr-pale-blue: hsl(221, 100%, 96%);
    --clr-light-lavender: hsl(241, 100%, 89%);
    --clr-dark-gray-blue: hsl(224, 30%, 27%);
}

html {
    font-family: 'Hanken Grotesk', sans-serif;
    font-weight: 500;
}

body {
    position: relative;
    min-height: 100vh;
    min-height: 100dvh;
    margin: 0;
    display: grid;
    place-items: center;
    padding-bottom: 3rem;
    font-size: 18px;
}

.result-container {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(2, min(50vw, 22.875rem));
    box-shadow: 0 0 2rem 0 var(--clr-pale-blue);
}

.result-container,
.result {
    border-radius: 2rem;
}

.result,
.summary {
    padding: 2rem 2.25rem;
}

.result {
    display: grid;
    background: var(--bg-gradient);
    color: white;
    text-align: center;
    gap: 1rem;
}

.result__title {
    font-size: 1.25em;
    color: var(--clr-light-lavender);
    font-weight: 700;
}

.result__quality-container {
    display: grid;
    gap:.5rem;
}

.result__quality {
    font-size: 1.75em;
    font-weight: 700;
}

.result__quality-container > p {
    color: var(--clr-light-lavender);
    margin: 0;
}

.result__score-container {
    margin-inline: auto;
    width: 12rem;
    height: 12rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    place-items: center;
    border-radius: 50%;
    background: var(--circle-gradient);
    color: hsla(0, 0%, 100%, 50%);
}

.result__score {
    color: white;
    font-weight: 800;
    font-size: 4em;
    line-height: 1.1;
}

.summary {
    display: grid;
    gap: 1rem;
    grid-template-rows: 1fr repeat(4, auto) 1fr;
}

.summary__title {
    color: var(--clr-dark-gray-blue);
}

.summary__block {
    display: flex;
    gap: 1rem;
    padding: 1rem;
    align-items: center;
    border-radius: .75rem;
}

.summary__icon {
    display: flex;
}

.summary__score {
    margin-left: auto;
    color: var(--clr-dark-gray-blue);
}

.fw-800 {
    font-weight: 800;
}

.opacity-50 {
    opacity: 50%;
}

.summary__block:nth-of-type(1) {
    color: hsl(var(--clr-primary-red-hsl));
    background: hsla(var(--clr-primary-red-hsl), 5%);
}

.summary__block:nth-of-type(2) {
    color: hsl(var(--clr-primary-orange-hsl));
    background: hsla(var(--clr-primary-orange-hsl), 5%);
}

.summary__block:nth-of-type(3) {
    color: hsl(var(--clr-primary-teal-hsl));
    background: hsla(var(--clr-primary-teal-hsl), 5%);
}

.summary__block:nth-of-type(4) {
    color: hsl(var(--clr-primary-blue-hsl));
    background: hsla(var(--clr-primary-blue-hsl), 5%);
}

.summary__btn {
    border: none;
    outline: transparent;
    background: var(--clr-dark-gray-blue);
    color: white;
    font-family: "Hanken Grotesk", sans-serif;
    font-size: 1em;
    font-weight: 700;
    border-radius: 100vw;
    padding: 1rem;
    height: fit-content;
    align-self: flex-end;
    cursor: pointer;
    &:hover,&:focus-visible {
        background: var(--bg-gradient)
    }
}

.attribution {
    position: absolute;
    bottom: 0;
    height: 2rem;
}

@media screen and (max-width: 768px) {
    .result-container {
        grid-auto-flow: row;
        grid-template-columns: auto;
        box-shadow: none;
        border-radius: 0;
    }
    .result {
        border-radius: 0 0 2rem 2rem;
        padding: 0 5rem 3rem 5rem;
    }
}